#navbar{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background-color: whitesmoke;
    height: 6vh;
    position: sticky;
    top: 0;
    z-index: 2;
    box-shadow: 0px 7px 15px -8px rgba(40,116,240,1);
    -webkit-box-shadow: 0px 7px 15px -8px rgb(3, 3, 3);
    -moz-box-shadow: 0px 7px 15px -8px rgb(255, 255, 255);
}

#input-bar {
    display: flex;
    align-items: center;
    border: 1px solid grey;
    border-radius: 5px;
    padding: 0px 2px;
    min-width: 30%;
}

#input-bar input {
    background-color: whitesmoke;
    outline: none;
    border: none;
    padding: 8px;
}

#quote{
    display: flex;
    align-items: center;
}

#quote h3 {
    padding-left: 20px;
}
